import React, { useState } from "react"
import { Link } from "react-router-dom"
import account from "../images/account.svg"
import styles from "../styles/user.module.scss"
import Stats from "../images/stats.svg"
import Awards from "../images/awards.svg"
import Modules from "../images/modules.svg"

const User = ({ children }) => {
  const percent = localStorage.getItem("percent")

  const [percentage, setPercentage] = useState(percent ? percent : 0)

  return (
    <div className={styles.user}>
      <div className={styles.topMenu}>
        <img className={styles.account} src={account} alt="Person Icon" />
      </div>

      {children}
      <div className={styles.bottomMenu}>
        <div className={styles.icons}>
          <img src={Stats} alt="Bars Icon" />
          <img src={Awards} alt="Star Icon" />
          <Link to="/modules">
            <img src={Modules} alt="Navigation Icon" />
          </Link>
        </div>
        <div className={styles.progress}>
          <span className={styles.progressPercentage}>{`${percentage}%`}</span>
        </div>
      </div>
    </div>
  )
}

export default User
