import React, { useState } from "react"
import { Link } from "react-router-dom"
import styles from "../styles/form.module.scss"
import logo from "../images/icon.svg"
import backBtn from "../images/back.svg"
import questionMark from "../images/question.svg"

const App = () => {
  return (
    <div className={styles.form}>
      <img src={logo} alt="Logo" className={styles.logo} />
      <form className={styles.formContent}>
        <label>
          Email:
          <br />
          <input type="email" />
        </label>

        <label>
          Password:
          <br />
          <input type="password" />
        </label>
        <Link to="/modules">
          <button className={styles.submit} type="submit">
            Login
          </button>
        </Link>
        <button className={styles.reset}>
          <img src={questionMark} alt="question mark icon" /> Forgot your
          password?
        </button>
        <Link to="/">
          <button className={styles.formBack}>
            <img className={styles.back} src={backBtn} alt="back button icon" />
          </button>
        </Link>
      </form>
    </div>
  )
}

export default App
