import React, { useState, useEffect } from "react"
import styles from "../styles/metacognition.module.scss"
import CardRight from "../images/card-right.svg"
import CardLeft from "../images/card-left.svg"
import { BrowserRouter, Route } from "react-router-dom"
import { useSwipeable, Swipeable } from "react-swipeable"

const KnowledgeCard = props => (
  <div className={styles.knowledgeCard}>
    <p className={styles.statement}>{props.text}</p>
    <div className={styles.arrows}>
      <img onClick={props.handleLeft} src={CardLeft} alt="Left Arrow" />
      <img onClick={props.handleRight} src={CardRight} alt="Right Arrow" />
    </div>
  </div>
)

const KnowledgeCards = () => {
  useEffect(() => {
    localStorage.setItem("cardsCorrect", 0)
  }, [])
  const [currentCard, setCurrentCard] = useState(1)

  const handleCorrectAnswer = () => {
    const changeScore = () => {
      let currentScore = JSON.parse(localStorage.getItem("cardsCorrect"))
      return localStorage.setItem("cardsCorrect", currentScore + 1)
    }

    const actions = [changeScore(), setCurrentCard(currentCard + 1)]
    return actions
  }

  return (
    <div>
      {currentCard === 1 && (
        <Swipeable
          onSwipedLeft={() => setCurrentCard(currentCard + 1)}
          onSwipedRight={() => handleCorrectAnswer()}
        >
          <KnowledgeCard
            handleLeft={() => setCurrentCard(currentCard + 1)}
            handleRight={() => handleCorrectAnswer()}
            text="Being 100% sure that you can define Metacognition."
          />{" "}
        </Swipeable>
      )}

      {currentCard === 2 && (
        <Swipeable
          onSwipedRight={() => setCurrentCard(currentCard + 1)}
          onSwipedLeft={() => handleCorrectAnswer()}
        >
          <KnowledgeCard
            handleLeft={() => handleCorrectAnswer()}
            handleRight={() => setCurrentCard(currentCard + 1)}
            text="Knowing you need to study harder for fill in the blank tests."
          />
        </Swipeable>
      )}

      {currentCard === 3 && (
        <Swipeable
          onSwipedLeft={() => setCurrentCard(currentCard + 1)}
          onSwipedRight={() => handleCorrectAnswer()}
        >
          <KnowledgeCard
            handleLeft={() => setCurrentCard(currentCard + 1)}
            handleRight={() => handleCorrectAnswer()}
            text="Knowing that you got all the answers correct after a test."
          />
        </Swipeable>
      )}

      {currentCard > 3 && (
        <p className={styles.completedText}>
          You have completed the knowledge check. Select the right arrow key to
          continue.
        </p>
      )}
    </div>
  )
}

export default KnowledgeCards
