import React, { useState } from "react"
import { BrowserRouter, Route, Link, Switch, Router } from "react-router-dom"
import Module from "./module"
import styles from "../styles/modules.module.scss"
import Metacognition from "../images/metacognition.svg"
import Rights from "../images/rights.svg"
import Lock from "../images/lock.svg"

const Modules = () => {
  return (
    <div className={styles.modules}>
      <Link to="/modules/metacognition">
        <Module icon={Metacognition} alt="Brain icon" name="Metacognition" />
      </Link>
      <Link style={{ pointerEvents: "none" }} to="/modules">
        <img className={styles.lock} src={Lock} alt="lock icon" />
        <Module icon={Rights} alt="Brain icon" name="Bill of Rights" />
      </Link>
    </div>
  )
}

export default Modules
