import React, { useState, useEffect } from "react"
import { BrowserRouter, Route, Link, Switch, Router } from "react-router-dom"
import ModulesLayout from "../components/modulesLayout"
import styles from "../styles/metacognition.module.scss"
import Previous from "../images/prevPage.svg"
import Next from "../images/nextPage.svg"
import Pentagon from "../images/pentagon.svg"
import {
  Page1,
  Page2,
  Page3,
  Page4,
  Page5,
  Page6,
  Page7,
  Page8,
  Page9,
  Page10
} from "../components/metacognition"
import User from "../components/user"

const Metacognition = ({ match }) => {
  let page = JSON.parse(localStorage.getItem("page"))

  const [currentPage, setCurrentPage] = useState(page ? page : 1)
  const nextHandler = () => {
    if (currentPage < 10) {
      localStorage.setItem("page", currentPage + 1)
      setCurrentPage(JSON.parse(localStorage.getItem("page")))
    }
  }

  const backHandler = () => {
    localStorage.setItem("page", currentPage > 1 ? currentPage - 1 : 1)
    setCurrentPage(JSON.parse(localStorage.getItem("page")))
  }

  const handleLessonName = () => {
    const page = JSON.parse(localStorage.getItem("page"))

    if (page <= 2) {
      return "Introduction"
    }
    if (page === 6 || page === 7) {
      return "Measuring Metacognition"
    } else {
      return "Knowledge Check"
    }
  }

  const handleLessonNumber = () => {
    const page = JSON.parse(localStorage.getItem("page"))

    if (page <= 5) {
      return 1
    } else {
      return 2
    }
  }

  return (
    <User>
      <div className={styles.module}>
        <div className={styles.modulesHeader}>
          <div className={styles.lessonNumber}>
            <img src={Pentagon} alt="Pentagon Icon" />
            <span className={styles.number}>{handleLessonNumber()}</span>
          </div>
          <p className={styles.name}>{handleLessonName()}</p>
        </div>
        <div className={styles.pages}>
          <div
            style={{ display: currentPage === 1 ? "flex" : "none" }}
            className={styles.page1}
          >
            <Page1 />
          </div>

          <div
            style={{ display: currentPage === 2 ? "flex" : "none" }}
            className={styles.page2}
          >
            <Page2 />
          </div>
          <div
            style={{ display: currentPage === 3 ? "flex" : "none" }}
            className={styles.page3}
          >
            <Page3 />
          </div>
          <div
            style={{ display: currentPage === 4 ? "flex" : "none" }}
            className={styles.page4}
          >
            <Page4 />
          </div>

          <div
            style={{ display: currentPage === 5 ? "flex" : "none" }}
            className={styles.page5}
          >
            <Page5 />
          </div>
          <div
            style={{ display: currentPage === 6 ? "flex" : "none" }}
            className={styles.page6}
          >
            <Page6 />
          </div>
          <div
            style={{ display: currentPage === 7 ? "flex" : "none" }}
            className={styles.page7}
          >
            <Page7 />
          </div>
          <div
            style={{ display: currentPage === 8 ? "flex" : "none" }}
            className={styles.page8}
          >
            <Page8 />
          </div>
          <div
            style={{ display: currentPage === 9 ? "flex" : "none" }}
            className={styles.page9}
          >
            <Page9 />
          </div>
          <div
            style={{ display: currentPage === 10 ? "flex" : "none" }}
            className={styles.page10}
          >
            <Page10 />
          </div>
        </div>{" "}
        <div className={styles.pageButtons}>
          {currentPage > 1 && (
            <button className={styles.prevPage} onClick={() => backHandler()}>
              <img src={Previous} alt="Arrow Left" />
            </button>
          )}
          {currentPage < 10 && (
            <button className={styles.nextPage} onClick={() => nextHandler()}>
              <img src={Next} alt="Arrow Right" />
            </button>
          )}

          {currentPage === 10 && (
            <Link style={{ width: "10rem" }} to="/modules">
              <button
                onClick={() => localStorage.setItem("percent", 50)}
                className={styles.return}
              >
                Finish
              </button>
            </Link>
          )}
        </div>
        <Link to="/modules">
          <button className={styles.backModules}>Back to Modules</button>
        </Link>
      </div>
    </User>
  )
}

export default Metacognition
