import React, { useState, useEffect } from "react"
import styles from "../styles/metacognition.module.scss"
import Intro1 from "../images/intro-thinking.svg"
import Intro2 from "../images/intro-flavell.svg"
import Intro3 from "../images/intro-meta-components.svg"
import Measuring1 from "../images/measuring-1.svg"
import Measuring2 from "../images/measuring-2.svg"
import { BrowserRouter, Route } from "react-router-dom"
import KnowledgeCards from "./knowledgeCards"

const Page1 = () => (
  <div>
    <img src={Intro1} alt="Lesson Graphic" />
    <p>
      <b>Metacognition </b>can be defined as "knowledge and cognition about
      cognitive phenomena" or in much simpler terms can be defined as "thinking
      about thinking".{" "}
    </p>
  </div>
)

const Page2 = () => (
  <div>
    <img className={styles.photo} src={Intro2} alt="Lesson Graphic" />

    <b className={styles.heading}>
      <p>John Flavell</p>
    </b>
    <b className={styles.heading}>
      <p>Developmental Psychologist</p>
    </b>
    <br />
    <img className={styles.arrows} src={Intro3} alt="Lesson Graphic" />
    <br />

    <p className={styles.text}>
      The work on metacognition is widely attributed to <b>John Flavell.</b>{" "}
      Flavell defined two main components of metacognition.{" "}
    </p>
    <hr />
    <p className={styles.text}>
      The first is known as <b>metacognitive knowledge.</b> Which can be
      described as reflecting on one's declarative knowledge and beliefs about
      the factors that might influence a cognitive task. In other words, being
      aware that some aspects of something you do can make it more difficult or
      easier for you. This can be knowing that you perform better in multiple
      choice answers over true / false because you find true / false questions
      tricky.
    </p>
    <hr />
    <p className={styles.text}>
      The second component is known as <b>metacognitive experiences.</b> Which
      can be described as "cognitive or affective experiences accompanying and
      pertaining to any intellectual enterprise". In other words being aware of
      how well you are mastering material, or confidence in your answers in an
      exam. This can be saying that on a scale from 1 - 10 your certainty that
      your answer is correct is a 10.
    </p>
  </div>
)

const Page3 = () => (
  <div>
    <p className={styles.instructions}>
      Swipe right on the card or select the right arrow for metacognitive
      experiences, and swipe left or select the left arrow for metacognitive
      knowledge.
    </p>
    <KnowledgeCards />
  </div>
)

const Page4 = props => {
  return (
    <div>
      <p className={styles.text}>
        Out of the 3 questions, how many do you think you answered correctly?
      </p>
      <form>
        <label>
          <span>Response: </span>
          <input
            onChange={e => {
              localStorage.setItem("metaCheck1", e.target.value)
            }}
            type="number"
          />
        </label>
      </form>
    </div>
  )
}

const Page5 = props => {
  return (
    <div>
      <p>
        You believed you would get{" "}
        <b>{JSON.parse(localStorage.getItem("metaCheck1"))}</b> answers correct
        and got <b>{JSON.parse(localStorage.getItem("cardsCorrect"))} </b>
        correct.{" "}
      </p>
    </div>
  )
}

const Page6 = props => {
  return (
    <div>
      <p>
        The measurement of metacognition is generally done by asking someone
        performing a task to judge their performance before they perform the
        task <b>(prospectively)</b> or after they perform the task{" "}
        <b>(retrospectively)</b>.
      </p>
      <img src={Measuring1} alt="lesson graphic" />
      <p>
        After the previous knowledge check you were asked to declare how many
        you believed you answered correctly. Since it was done after the task,
        that would have been an example of asking you to make a retrospective
        judgment of your performance.
      </p>
    </div>
  )
}

const Page7 = props => {
  return (
    <div>
      <p>
        <b>Calibration</b> is the comparison of your performance judgement with
        how well you actually perform. The results can let you know if you are
        evaluating your own performance with accuracy. Research suggests that
        being well-calibrated increases academic outcomes and your ability to
        predict your performance and awareness of your knowledge. This is
        important because once you enter the political science modules you will
        want to discover how much more time you should spend on certain
        material, and if you are underconfident or overconfident in what you
        believe to know.
      </p>
      <br />
      <hr />
      <h2>Calibration Example</h2>
      <img src={Measuring2} alt="Lesson Graphic" />
      <p style={{ textAlign: "center" }}>Correct: 1</p>
      <p style={{ textAlign: "center" }}>Predicted: 3</p>
      <hr />
      <p>
        In this scenario you would have thought you responded all 3 correctly.
        But only got 1 correct.
        <br />
        You were overconfident by 2 responses.
      </p>
      <br />
      <br />
      <p>In other words you were overconfident by 66% or .66 (2 / 3).</p>
    </div>
  )
}

const Page8 = props => {
  const [option, setOption] = useState("")
  const [correct, setCorrect] = useState(0)

  console.log(option)

  const handleSubmit = () => {
    const page = JSON.parse(localStorage.getItem("page"))
    localStorage.setItem("correct", correct)
    localStorage.setItem("page", page + 1)
  }

  return (
    <div>
      <p className={styles.problem}>
        A student has taken a political science test with 10 questions. The
        student has judged his performance and decided that 8 questions were
        answered correctly. The student actually got all the questions
        correctly.
      </p>
      <br />
      <p>The student was...</p>

      <div className={styles.options}>
        <button onClick={() => setCorrect(correct + 1)}>Underconfident</button>{" "}
        <b style={{ fontSize: "3rem" }}>or</b>
        <button>Overconfident</button>
      </div>
      <p>By how many questions?</p>
      <form>
        <input
          onInput={e => {
            if (e.target.value == 2) {
              setCorrect(correct + 1)
            } else {
              setCorrect(correct)
            }
          }}
          type="number"
        />
      </form>
    </div>
  )
}

const Page9 = props => {
  return (
    <div>
      <p>
        Out of the 2 questions, how many do you think you answered correctly?
      </p>
      <form>
        Response:{" "}
        <input
          onChange={e => {
            localStorage.setItem("metaCheck2", e.target.value)
          }}
          type="number"
        />
        <button onClick={props.handleSubmit} className={styles.submit}>
          Submit
        </button>
      </form>
    </div>
  )
}

const Page10 = props => {
  return (
    <div>
      <div>
        <p>
          You believed you would get{" "}
          <b>{JSON.parse(localStorage.getItem("metaCheck2"))}</b> answers
          correct and got <b>{JSON.parse(localStorage.getItem("correct"))}</b>{" "}
          correct.
        </p>
      </div>
      <hr />
      <br />
      <p>
        You have completed the <b>Metacognition </b>module!
      </p>
      <br />
      <p>
        In this module you have learned about <b>Metacognition </b>and the
        importance of <b>Calibration</b>. <br />
        <br />
        You are now ready to learn, test your knowledge, and continue to improve
        on your calibration.
      </p>
    </div>
  )
}

export { Page1, Page2, Page3, Page4, Page5, Page6, Page7, Page8, Page9, Page10 }
