import React, { useState } from "react"
import { Link } from "react-router-dom"
import styles from "../styles/form.module.scss"
import logo from "../images/icon.svg"
import backBtn from "../images/back.svg"

const App = () => {
  const [name, setName] = useState("")
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")

  const handleSubmit = () => {
    localStorage.setItem("name", name)
    localStorage.setItem("email", email)
    localStorage.setItem("password", password)
  }

  return (
    <div className={styles.form}>
      <img src={logo} alt="Logo" className={styles.logo} />
      <h1 className={styles.title}>Easy Sign Up.</h1>
      <p className={styles.subtitle}>
        Enjoy learning <b>completely free</b>.
      </p>
      <form className={styles.formContent}>
        <label>
          Name:
          <br />
          <input onInput={e => setName(e.target.value)} type="text" />
        </label>

        <label>
          Email:
          <br />
          <input onInput={e => setEmail(e.target.value)} type="email" />
        </label>

        <label>
          Password:
          <br />
          <input onInput={e => setPassword(e.target.value)} type="password" />
        </label>
        <Link to="/login">
          <button
            onClick={() => handleSubmit()}
            className={styles.submit}
            type="submit"
          >
            Submit
          </button>
        </Link>

        <Link to="/">
          <button className={styles.formBack}>
            <img className={styles.back} src={backBtn} alt="back button icon" />
          </button>
        </Link>
      </form>
    </div>
  )
}

export default App
